.side-drawer {
  height: 100%;
  background: white;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 800;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -5em;
}

.side-drawer ul li a {
  margin: 0.5rem 0;
  text-transform: uppercase;
  text-decoration: none;
  color:black;
  display: block;
  padding: 1em 1.4em;
  font-size: 1.3em;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: darkslategray;
}

@media screen and (min-width: 796px) {
  .side-drawer {
    display: none;
  }
}
