@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container {
    position: absolute;
    padding: 0;
    margin-top: 3em;
}

.ban-content-container {
    display: flex;
    flex-direction: column;
}

.main-cont-banner, 
.white-banner-1, 
.white-banner-2,
.quote-banner, 
.order-now-prompt,
.first-img-section,
.second-img-section {
    text-align: center;
    width: 100%;
    z-index: 0;
}
.main-cont-banner {
    color: white;
    font-size: 35px;
    background-color: rgba(86,87,116,65%);
}

.white-banner-1, 
.white-banner-2{
    font-size: 35px;
    background-color: rgba(202, 199, 199, 0.925);
}

.quote-banner {
    color: white;
    font-size: 35px;
    background-color: rgba(86,87,116,65%);
}
.order-now-prompt{
    font-size: 35px;
    background-color: rgba(121, 121, 121, 0.911);
    color: black;
    height: 50vh;
}
#order-now-btn{
    border: none;
    border-radius: 15px;
    background-color: #52A870;
    width: 150px;
    height: 45px;
    font-size: 29px;
    font-family: 'Nanum Pen Script', cursive;
}

#order-now-btn:hover{
    background-color: #317047;
    color: rgb(209, 209, 209);
    cursor: pointer;
}
.banner-txt {
    padding: 0.3em 1.5em;
}
.mission-txt{
    color: white;
    padding: 0.8em 2em;
    font-weight: bold;
}
.quote-txt{
    text-transform: uppercase;
    color: black;
    padding: 0.5em 1.6em;
    font-weight: bold;
}
.banner-txt-img {
    color: rgb(179, 179, 179);
    padding: 0.8em 2em;
    font-weight: bold;
}

#chef-img {
    position: fixed;
    width: 100%;
    z-index: -1;
}
.first-img-section {
    display: inline-flex;
    background: url(/oogie_prod/static/media/food_pic.4b921bd9.svg) no-repeat center;
    background-size: cover;
    /* height: 20em; */
    height: 40vh;
}

#footer-txt{
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.5vh;
    color: rgb(70, 66, 66);
}

.second-img-section {
    background: url(/oogie_prod/static/media/chefs_long.0f04daad.svg) no-repeat center;
    background-size: 100em 100em;
    height: 17em;
    height: 30vh;
    display: inline-flex;
}
.transparent-bkgnd {
    height: 100%;
    width: 50%;
}

.first-img-section p, 
.second-img-section 
.transparent-bkgnd{
    font-size: 2.5vw;
}

.transparent-bkgnd-txt{
    padding: 0.8em 1.5em;
    color: white;
    font-size: bold;
}

#orange{
    background-color: rgba(204, 121, 39, 0.671);
}
#blue{
    background-color: rgba(86, 87, 116, 0.692);
}
.transparent-white {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.58);
}
#transparent-white-blk1,
#transparent-white-blk2 {
    color: rgb(110,105,105);
    float: left;
    padding-left: 8vw;
}

#transparent-white-blk2{
    padding-left: 5vw;
    padding-right: 45vw;
}

hr {
    margin-top: 100px;
    width: 75%;
    border: rgb(122, 120, 120) solid 1.85px;
}

@media screen and (max-width: 768px) {
    .main-content-web{
        display: none;
    }
}

@media screen and (min-width: 769px) {
    .main-content-mobile {
        display: none;
    }
    .home-container{
        margin-top: 0;
    }
    .order-now-prompt {
        position: absolute;
        justify-self: end;
        height: 100%;
        top: 70vh;
        font-size: 2.5vw;
    }
    #order-now-btn {
        width: 15vw;
        font-size: 2vw;
    }
}
.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 24px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button__line {
  width: 30px;
  height: 2px;
  background: white;
}

.toolbar {
  position: fixed;
  height: 100%;
  width: 20%;
  z-index: 500;
  background-color: rgb(255, 255, 255);
  top: 0;
  left: 0;
  border-right: rgba(128, 128, 128, 0.37) solid 1px;
}

.toolbar__nav {
  display: grid;
  grid-template-columns: 2 40px;
  padding: 0;
}

.toolbar_toggle-button{
  margin-left: auto;
  order: 2;
}

#toolbar__logo {
  align-self: center;
  width: 100%;
  height: 23em;
}

.toolbar_nav-items ul {
  list-style: none;
  font-size: 2.5vw;
  background-color: rgba(193, 193, 193, 0.185);
  height: 26vw;
  margin-top: 2vw;
}

.spacer {
  flex: 1 1;
}

.toolbar_nav-items li {
  padding: 1.8vw 1vw;
  border: none;
}

.toolbar_nav-items a {
  text-decoration: none;
  color: black;
}

.toolbar_nav-items a:hover,
.toolbar_nav-items a:active {
  color: rgb(191,191,191);
}

@media screen and (max-width: 768px) {
  .toolbar_nav-items {
    display: none;
  }
  .toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #f3d5d5;
    height: 56px;
    z-index: 500;
  }
  .toolbar__nav {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
  }
  .toolbar__logo img{
    display: none;
  }
  #logo-active{
    margin-left: 0.8em;
    color: black;
    text-decoration: none;
    font-size: 1.9rem;
  }
}

@media screen and (min-width: 769px) {
  .toolbar_toggle-button {
    display: none;
  }

  .toolbar__logo {
    margin-left: 0;
  }
  #logo-active{
    display: none;
  }
}

.side-drawer {
  height: 100%;
  background: white;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 800;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.side-drawer.open {
  transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -5em;
}

.side-drawer ul li a {
  margin: 0.5rem 0;
  text-transform: uppercase;
  text-decoration: none;
  color:black;
  display: block;
  padding: 1em 1.4em;
  font-size: 1.3em;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: darkslategray;
}

@media screen and (min-width: 796px) {
  .side-drawer {
    display: none;
  }
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgb(0, 0, 0, 0.3);
  z-index: 700;
}

body, html{
    height: 100%;
    margin: 0;
    font-family: 'Nanum Pen Script', cursive;
    max-width: 100%;
}

body {
    background-color: rgb(255,239,239);
    background-repeat: no-repeat;
    background-attachment: fixed;
}


@media screen and (min-width: 769px){
    .app-container{
        display: grid;
        grid-template-columns: 20% auto;
        margin: 0;
        padding: 0;
    }

}
